.form-group .input-loader {
  position: absolute;
  right: 15px;
  top: 50%;
  background: var(--white);
  opacity: 1;
  font-size: 1.2em;
}

.form-group .is-invalid ~ .input-loader {
  top: 41%;
}

.v-loader.global {
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}

.v-loader .circular {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  margin-top: -50px;
  margin-left: -50px;
  height: 100px;
  width: 100px;
  animation: loader-rotate 2s linear infinite;
}

.v-loader .circular > .path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: loader-dash 1.5s ease-in-out infinite, loader-color 6s ease-in-out infinite;
  stroke-linecap: round;
}

@keyframes loader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124;
  }
}

@keyframes loader-color {
  100%, 0% {
    stroke: #d62d20;
  }
  40% {
    stroke: #0057e7;
  }
  66% {
    stroke: #008744;
  }
  80%, 90% {
    stroke: #ffa700;
  }
}

