/**
 * Responsive vuetable
 */
@media only screen and (max-width: 760px), (min-device-width: 768px) and (max-device-width: 1024px) {
  /* Force table to not be like tables anymore */
  table.vuetable, .vuetable thead, .vuetable tbody, .vuetable th, .vuetable td, .vuetable tr {
    display: block;
  }
  /* Hide table headers (but not display: none;, for accessibility) */
  .vuetable thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .vuetable tr {
    border: 1px solid #ccc;
    border-bottom: 0;
  }
  .vuetable td {
    /* Behave  like a "row" */
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
    padding-left: 50% !important;
  }
  .vuetable td:before {
    /* Now like a table header */
    position: absolute;
    /* Top/left values mimic padding */
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
  }
  /*
        Remember to label the data ie.
        td:nth-of-type(1):before { content: "First Name"; }
    */
}

